@use "global" as *;

.page-campaign {
  position: relative;
}

.page-campaign::before {
  content: "";
  background-image: url(../images/common/goldfish-sp.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  position: absolute;
  top: rem(-24);
  left: rem(15);
  width: rem(120);
  height: rem(55);

  @include mq(md) {
    background-image: url(../images/common/goldfish-pc.svg);
    top: rem(-70);
    left: calc(50% - min(48%, rem(620)));
    width: rem(301);
    height: rem(138);
  }
}

// グリッドは.campaign-listで管理 25.2.3
.page-campaign__items {
  // display: grid;
  // gap: rem(40);
  padding: 0 rem(32) 0 rem(33); // カードにwidthをつけずにpaddingで調整 25.8.21
  margin-top: rem(40);

  @include mq(md) {
  //   grid-template-columns: repeat(2, 1fr);
  //   gap: rem(80) rem(40);
  padding: 0;
  }
}
